import React from "react";
import ProductComponent from "components/ProductComponent";

const ServicesPage: React.FC = () => {
  const signupUrl = process.env.REACT_APP_SIGNUP_URL || "#";

  return (
    <div>
      {/* Header Section */}
      <header className="bg-blue-600 text-white text-center px-4 py-20">
        <div className="container mx-auto">
          <h1 className="text-4xl font-bold mb-4">Layanan Kami</h1>
          <p className="text-xl mb-8">
            Temukan berbagai layanan yang kami tawarkan untuk membantu bisnis
            Anda berkembang.
          </p>
        </div>
      </header>

      {/* Services Section */}
      <main>
        <section id="services" className="py-20 bg-gray-100 px-4">
          <div className="container mx-auto text-center">
            <h2 className="text-3xl font-bold mb-8">Apa yang Kami Tawarkan</h2>
            <div className="flex flex-wrap justify-center items-stretch">
            <ProductComponent title="Manajemen Produk">
                Manajemen Produk Invntt memungkinkan Anda untuk menambah,
                memperbarui, dan menghapus produk dengan mudah, melacak stok,
                dan mengatur peringatan untuk stok rendah. Selain itu, Anda bisa
                mengelola harga, diskon, dan penawaran khusus dengan praktis.
              </ProductComponent>
              <ProductComponent title="Manajemen Pesanan">
                Manajemen Pesanan memudahkan pembuatan dan modifikasi pesanan,
                pelacakan status pesanan, serta pembuatan dan pengiriman struk
                dan faktur melalui email, memastikan proses yang efisien dan
                teratur.
              </ProductComponent>
              <ProductComponent title="Pemrosesan Pembayaran">
                Pemrosesan Pembayaran dengan Invntt memungkinkan Anda memproses
                pembayaran menggunakan QRIS dan berbagai metode lain seperti
                kartu kredit/debit, e-wallet, dan transfer bank. Semua transaksi
                dicatat dalam riwayat pembayaran untuk kemudahan pengelolaan
                keuangan.
              </ProductComponent>
              <ProductComponent title="Pelaporan Keuangan">
                Pelaporan Keuangan Invntt menyediakan laporan penjualan
                terperinci untuk berbagai periode, pelacakan pengeluaran bisnis,
                serta laporan Laba Rugi untuk analisis kinerja bisnis yang lebih
                baik.
              </ProductComponent>
              <ProductComponent title="Manajemen Pelanggan">
                Manajemen Pelanggan memungkinkan penyimpanan dan pengelolaan
                informasi pelanggan, implementasi program loyalitas, serta
                pengumpulan dan pengelolaan umpan balik pelanggan untuk
                meningkatkan layanan dan produk Anda.
              </ProductComponent>
            </div>
          </div>
        </section>
      </main>

      {/* Call to Action Section */}
      <footer className="bg-blue-600 text-white text-center px-4 py-20">
        <div className="container mx-auto">
          <h2 className="text-3xl font-bold mb-4">Siap untuk Memulai?</h2>
          <p className="text-xl mb-8">
            Rasakan manfaat dari layanan kami dan bawa bisnis Anda ke tingkat
            berikutnya.
          </p>
          <a
            href={signupUrl}
            className="bg-white text-blue-600 font-semibold py-2 px-4 rounded"
          >
            Daftar Sekarang
          </a>
        </div>
      </footer>
    </div>
  );
};

export default ServicesPage;
